.list {
  list-style: none;
  padding: 0;
}

.items {
  display: flex;
  justify-content: space-between;

  width: 400px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;

  outline: 3px solid rgb(50, 4, 71);
  background-color: #e499c2;
  border-radius: 4px;
}

.content {
  font-size: 20px;

  color: blueviolet;
}

.btn {
  width: 100px;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;

  font-size: 15px;

  color: rgb(16, 3, 63);
  background-color: rgb(209, 205, 205);
  border-radius: 5px;
}

.btn:hover,
.btn:focus {
  color: rgb(242, 244, 247);
  background-color: rgb(229, 38, 13);
}