.form {
  display: flex;

  flex-direction: column;
  gap: 30px;
  width: 400px;

  outline: 3px solid rgb(14, 12, 15);
  background-color: #e499c2;

  margin: 0 auto;
  border-radius: 5px;
  padding: 20px;
}

.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 45%;

  font-size: 20px;

  color: blueviolet;
}

.input {
  border-color: rgb(45, 93, 248);
  border-radius: 4px;
}

.btn {
  width: 100px;
  cursor: pointer;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;

  font-size: 15px;

  color: rgb(16, 3, 63);
  background-color: rgb(245, 238, 242);
}

.btn:hover {
  color: rgb(242, 244, 247);
  background-color: rgb(59, 144, 10);
}